//reducer.js
import { ADD_COUNT, SUB_COUNT } from "../constants";

// 
let initialState = {
  count: 0,
};
export default function reducer(state = initialState, action) {
  switch (action.type) {
    case ADD_COUNT:
      return {
        count: state.count + Number(action.text),
      };
    case SUB_COUNT:
      return {
        count: state.count - action.text,
      };
    default:
      return state;
  }
}
